.ImageSelectionList {
    display: grid;
    align-items: center;
    justify-content: center;
    gap: 1em;
    max-height: 500px;
}

.ImageSelectionListItem {
    width: 100px;
    height: 100px;
    background-size: cover;
    background-position: center;
    transition: 0.16s;
}

.ImageSelectionListItem:hover {
    box-shadow: 0px 0px 2em;
}

.ImageSelectionListItem:active {
    box-shadow: 0px 0px 2em 0.3em;
}