.ArrowButton {
    background-color: rgb(255,255,255,0.4);
    font-size: 2em;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 1.5em;
}

.Gallery {
    width: 100%;
    height: 0;
    background-size: cover;
    background-position: center;
    position: relative;
    transition: 1s;
}

.GalleryScreen {
    width:60%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.GalleryWrapper {
    display: flex;
    justify-content: space-around;
}